import { Link } from "react-router-dom";
import MapView from "../components/Map";
import "../styles/Principal.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMapLocationDot } from "@fortawesome/free-solid-svg-icons";

// IMAGEES

import car_icon_1 from "../img/car-icon-1.webp";
import car_icon_2 from "../img/car-icon-2.webp";
import car_icon_3 from "../img/car-icon-3.webp";
import car_icon_4 from "../img/car-icon-4.webp";
import car_icon_5 from "../img/car-icon-5.webp";
import car_icon_6 from "../img/car-icon-6.webp";
import qs1 from "../img/personal.png";
import qs2 from "../img/horarioatencion.png";
import qs3 from "../img/direccion.png";
import wspicon from "../img/icon-wsp-chat.webp";

import imgIVETEC1 from "../img/IVETECimg1.webp";
import imgIVETEC2 from "../img/IVETECimg2.webp";
import imgIVETEC3 from "../img/IVETECimg3.webp";
import imgIVETEC4 from "../img/IVETECimg4.webp";

function PrincipalPage(){
    return(
        <div>
            <section className = "section-nosotros" id = "nosotros">
                <section className = "section-carousel">
                    <img src = {imgIVETEC1} />
                    <img src = {imgIVETEC2} />
                    <img src = {imgIVETEC3} />
                    <img src = {imgIVETEC4} />
                    {/* <AutoPlay/>      */}
                </section>  
                <div className = "section-nosotros-container">
                    <div className = "section-nosotros-left">
                            <h2 className = "section-nosotros-left-quienes-somos">. : ¿Quiénes somos? : .</h2> 
                            <p className = "section-nosotros-left-quienes-somos-text">En IVETEC PERU, tenemos como objetivo contribuir en el buen funcionamiento y mantenimiento de su vehículo para aportar con la seguridad vial del Perú. Por lo cual, contamos con:</p>
                        <div className = "section-nosotros-left-caracteristicas">
                            <div className = "section-nosotros-left-caracteristica">
                                <img className = "caracteristica-img" src = {qs1} alt = "personal.jpeg"/>
                                <div className = "section-nosotros-left-caracteristica-detalle">
                                    <span className = "detalle-span">Personal altamente calificado</span>
                                    <p>Contará a su disposición con el apoyo de personal altamente preparado que garantiza la calidad de su correcta revisión técnica vehicular.</p>
                                </div>
                            </div>
                            <div className = "section-nosotros-left-caracteristica">
                                <img className = "caracteristica-img" src = {qs2} alt = "horario.png" />
                                <div className = "section-nosotros-left-caracteristica-detalle">
                                    <span className = "detalle-span">Horario de atención</span>
                                    <p>Para su pronta atención puede comunicarse con nosotros en los siguientes horarios: Lunes a Sábado de 8am a 9pm y Domingos de 8am a 2pm</p>
                                </div>
                            </div>
                            <div className = "section-nosotros-left-caracteristica">
                                <img className = "caracteristica-img" src = {qs3} alt = "direccion.png" />
                                <a href = "#map" className = "icon-link-map">
                                    <div className = "container-icon-map">
                                        <FontAwesomeIcon className = "icon-map" icon = {faMapLocationDot} />
                                        <span>Ver</span>
                                    </div>
                                </a>
                                <div className = "section-nosotros-left-caracteristica-detalle">
                                    <span className = "detalle-span">Dirección</span>
                                    <p>Puede visitarnos en la siguiente dirección: Av. Mar del Norte MZ D. Lote 04 Jicamarca. Referencia: Portón de Jicamarca a la mano derecha a 2 cuadras.</p>
                                </div>
                            </div>
                        </div>
                    </div> 
                </div> 
            </section>
            <div className = "wave-nosotros">
                <svg width="100%" height="100%" id="svg" viewBox="0 0 1440 390" xmlns="http://www.w3.org/2000/svg" class="transition duration-300 ease-in-out delay-150"><defs><linearGradient id="gradient" x1="50%" y1="0%" x2="50%" y2="100%"><stop offset="5%" stop-color="#E6EAEB"></stop><stop offset="95%" stop-color="#125067"></stop></linearGradient></defs><path d="M 0,400 C 0,400 0,200 0,200 C 66.82296650717703,223.6937799043062 133.64593301435406,247.38755980861242 236,238 C 338.35406698564594,228.61244019138758 476.2392344497607,186.1435406698565 580,193 C 683.7607655502393,199.8564593301435 753.397129186603,256.0382775119617 853,256 C 952.602870813397,255.9617224880383 1082.1722488038276,199.70334928229667 1185,181 C 1287.8277511961724,162.29665071770333 1363.9138755980862,181.14832535885165 1440,200 C 1440,200 1440,400 1440,400 Z" stroke="none" stroke-width="0" fill="url(#gradient)" fill-opacity="1" class="transition-all duration-300 ease-in-out delay-150 path-0" transform="rotate(-180 720 200)"></path></svg>
            </div>
            <section className = "section-servicios" id = "servicios">
                <h1 className = "servicios-title">. : Servicios : .</h1>
                <div className = "container-servicios">
                    <div className = "servicio">
                        <img src = {car_icon_1} alt = "privado.png"/>
                        <div className = "servicio-datos">
                            <span className = "servicio-span">Atención a empresas Privadas y del Estado</span>
                            <p className = "servicio-text">Según se requiera</p>    
                        </div>            
                    </div>
                    <div className = "servicio">
                        <img src = {car_icon_2} alt = "particular.png"/>
                        <div className = "servicio-datos">
                            <span className = "servicio-span">Auto particular</span>
                            <p className = "servicio-text">SOAT vigente - Tarjeta de propiedad - Revisión técnica anterior, si pasa por primera vez exonerado - Certificado anual de gas vigente GLP/ GNV.</p>
                        </div>
                    </div>
                    <div className = "servicio">
                        <img src = {car_icon_3} alt = "taxi.png"/>
                        <div className = "servicio-datos">
                            <span className = "servicio-span">Servicio taxi</span>
                            <p className = "servicio-text">SOAT o CAT vigente - Tarjeta de propiedad - Revisión técnica anterior - Certificado anual de gas GLP/GNV - Tarjeta única de habilitación o permiso para el servicio de taxi.</p>
                        </div>
                    </div>
                    <div className = "servicio">
                        <img src = {car_icon_4} alt = "mercancia.png"/>
                        <div className = "servicio-datos">
                            <span className = "servicio-span">Transporte en mercancía general</span>
                            <p className = "servicio-text">SOAT vigente. Tarjeta de propiedad. Revisión técnica anterior. Certificado de habilitación vehicular de mercancías ( privado o cercanía general)</p>
                        </div>
                    </div>
                    <div className = "servicio">
                        <img src = {car_icon_5} alt = "complementario.png"/>
                        <div className = "servicio-datos">
                            <span className = "servicio-span">Servicios complementarios</span>
                            <p className = "servicio-text">Certificado de incorporación vehicular y complementaria - Certificado de hermeticidad - Certificado de limitador velocidad - Certificado de opacidad.</p>
                        </div>
                    </div>
                    <div className = "servicio">
                        <img src = {car_icon_6} alt = "mercanciapeligrosa.png"/>
                        <div className = "servicio-datos">
                            <span className = "servicio-span">Transporte de mercancía peligrosa</span>
                            <p className = "servicio-text">SOAT vigente - Tarjeta propiedad- Revisión técnica anterior - Resolución o permiso para el transporte de residuos peligrosos.</p>
                        </div>
                    </div>
                </div>
            </section>
            <div className = "wave-servicios">
                <svg width="100%" height="100%" id="svg" viewBox="0 0 1440 390" xmlns="http://www.w3.org/2000/svg" class="transition duration-300 ease-in-out delay-150"><defs><linearGradient id="gradient" x1="50%" y1="100%" x2="50%" y2="0%"><stop offset="5%" stop-color="#125067"></stop><stop offset="95%" stop-color="#e6eaeb"></stop></linearGradient></defs><path d="M 0,400 C 0,400 0,133 0,133 C 38.320927364656555,117.92770995190529 76.64185472931311,102.85541990381057 132,95 C 187.3581452706869,87.14458009618943 259.7535084474041,86.50603033666297 320,93 C 380.2464915525959,99.49396966333703 428.3441114810705,113.12045874953756 481,117 C 533.6558885189295,120.87954125046244 590.8700456283143,115.0121346651868 646,127 C 701.1299543716857,138.9878653348132 754.1757060056726,168.83100258971513 810,164 C 865.8242939943274,159.16899741028487 924.427130348995,119.66385497595265 978,110 C 1031.572869651005,100.33614502404735 1080.1157725983478,120.51357750647429 1134,134 C 1187.8842274016522,147.4864224935257 1247.109779257615,154.2818349981502 1299,153 C 1350.890220742385,151.7181650018498 1395.4451103711926,142.3590825009249 1440,133 C 1440,133 1440,400 1440,400 Z" stroke="none" stroke-width="0" fill="url(#gradient)" fill-opacity="0.53" class="transition-all duration-300 ease-in-out delay-150 path-0"></path><defs><linearGradient id="gradient" x1="50%" y1="100%" x2="50%" y2="0%"><stop offset="5%" stop-color="#125067"></stop><stop offset="95%" stop-color="#e6eaeb"></stop></linearGradient></defs><path d="M 0,400 C 0,400 0,266 0,266 C 67.23403625601183,279.6896781354051 134.46807251202367,293.3793562708102 184,295 C 233.53192748797633,296.6206437291898 265.3617462079171,286.17225305216425 317,277 C 368.6382537920829,267.82774694783575 440.0849426563078,259.93163152053273 501,269 C 561.9150573436922,278.06836847946727 612.2984831668516,304.1012208657047 658,313 C 703.7015168331484,321.8987791342953 744.7211246762856,313.6634850166482 797,310 C 849.2788753237144,306.3365149833518 912.817018128006,307.24483906770257 960,291 C 1007.182981871994,274.75516093229743 1038.0108028116906,241.35715871254163 1095,240 C 1151.9891971883094,238.64284128745837 1235.1397706252312,269.32652608213095 1297,279 C 1358.8602293747688,288.67347391786905 1399.4301146873845,277.3367369589345 1440,266 C 1440,266 1440,400 1440,400 Z" stroke="none" stroke-width="0" fill="url(#gradient)" fill-opacity="1" class="transition-all duration-300 ease-in-out delay-150 path-1"></path></svg>
            </div>
            <section className = "section-tarifas" id = "tarifas">
                <h2 className = "section-tarifas-title">. : Tarifas : .</h2>
                <table className = "table">
                    <thead>
                        <tr>
                            <th className = "cabecera-table">VEHÍCULOS</th>
                            <th className = "cabecera-table">PRECIOS (S/.)</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td className = "vehiculo-columna">Particulares</td>
                            <td className = "precio-columna">Desde S/.80</td>
                        </tr>
                        <tr>
                            <td className = "vehiculo-columna">Taxis</td>
                            <td className = "precio-columna">Desde S/.40</td>
                        </tr>
                        <tr>
                            <td className = "vehiculo-columna">Transporte urbano</td>
                            <td className = "precio-columna">Desde S/.60</td>
                        </tr>
                    </tbody>
                </table>
            </section>
            <section className = "section-contactenos" id = "map">
                <div className = "container-map" >
                    <MapView />
                </div>
                <div className = "container-contactanos">
                    <form className= "contactanos-formulario" id = "contactanos">
                        <h2 className = "contactanos-title">. : Contáctanos : .</h2>
                        <p className = "contactanos-text">Bríndenos sus datos para ponernos al contacto</p>
                        <div className = "contactanos-formulario-inputs">
                            <div className = "formulario-input">
                                <span className = "span-input">Nombre</span><input className = "input-item" required type = "text"></input>
                            </div>
                            <div className = "formulario-input">
                                <span className = "span-input">E-mail</span><input className = "input-item" required type = "email"></input>
                            </div>
                            <div className = "formulario-input">
                                <span className = "span-input">Teléfono</span><input className = "input-item" required type = "text"></input>
                            </div>
                            <div className = "formulario-input">
                                <span className = "span-input">Ciudad</span><input className = "input-item" required type = "text"></input>
                            </div>
                            <button className = "btn-enviar" type = "submit">Enviar</button>
                        </div>
                    </form>
                </div>
            </section>
            <Link className = "btn-wsp" target = "_blank" to = "https://api.whatsapp.com/send?phone=957034285"><img src = {wspicon} /></Link>
        </div>
    );
}

export default PrincipalPage;